<template>
  <div>
      <div class="post">
      <h1>{{ article.title }}</h1>
        <p class="text-muted">Posted on: {{ article.publishedAt }} by: {{ article.author || N/A }}</p>
        <p class="content">{{article.content}}</p>
  </div>
  </div>
</template>

<script>
export default {
    props: ['article'],
  data() {
    return {};
  }
};
</script>
<style scoped lang="scss">
.content {
    font-size: 1.3rem;
}
</style>



