<template>
  <div>

    <header class="text-center mb-3">
      <h1>Welcome to NewsCenter</h1>
      <h4>Check out some of the sections below to get started...</h4>
      <ul class="nav justify-content-center">
        <li class="nav-item">
          <a
            class="nav-link active"
            href="#"
            @click="getNews('https://newsapi.org/v2/everything?q=bitcoin&from=2019-02-22&sortBy=publishedAt&apiKey=3077a2e3e4f94552b071d1e2666fb582')"
          >Bitcoin</a>
        </li>
        <li class="nav-item">
          <a
            class="nav-link"
            href="#"
            @click="getNews('https://newsapi.org/v2/top-headlines?country=us&category=business&apiKey=3077a2e3e4f94552b071d1e2666fb582')"
          >Business</a>
        </li>
        <li class="nav-item">
          <a
            class="nav-link"
            href="#"
            @click="getNews('https://newsapi.org/v2/top-headlines?sources=techcrunch&apiKey=3077a2e3e4f94552b071d1e2666fb582')"
          >Technology</a>
        </li>
        <li class="nav-item">
          <a class="nav-link" href="#">Global</a>
        </li>
      </ul>

      <div class="search my-3">
        <input type="text" v-model="search" class="mr-1" placeholder="Search for other news...">
        <button class="btn btn-primary" @click="searchArticles">Search</button>
      </div>
    </header>
    <div class="alert alert-success w-25 m-auto" v-if="saveAlert">Article has been saved</div>
    <div class="article-container">
      <div class="card card-body shadow-sm" v-for="(article, index) in paginatedNews" :key="index">
        <img :src="article.urlToImage" alt>
        <h2>{{ article.title }}</h2>
        <small class="text-muted">Posted on: {{ article.publishedAt }}</small>
        <p>{{ article.description }}</p>
        <router-link :to="{ name: 'ViewArticle', params: {article} }">View full article</router-link>
        <button @click="saveArticle(article)" class="btn btn-sm btn-outline-primary">Save Article</button>
      </div>
    </div>

    <div class="page-nav d-flex justify-content-center mb-3">
      <button class="btn btn-primary mr-2" @click="prevPage">Previous</button>
      <button class="btn btn-primary" @click="nextPage">Next</button>
    </div>
  </div>
</template>
<style lang="scss" scoped>
</style>
<script>
import firebase from "firebase";
import axios from "axios";
import { db } from "../firebase";
export default {
  data() {
    return {
      currentNews: {
        articles: [],
        start: 0,
        stop: 5
      },
      search: "",
      saveAlert: false
    };
  },
  mounted() {
    axios
      .get(
        "https://newsapi.org/v2/everything?q=bitcoin&from=2019-02-22&sortBy=publishedAt&apiKey=3077a2e3e4f94552b071d1e2666fb582"
      )
      .then(res => (this.currentNews.articles = res.data.articles))
      .catch(err => console.log(err));
  },
  beforeCreate: function() {
    if (!this.$session.exists()) {
      this.$router.push({ name: "LogIn" });
    }
  },
  computed: {
    paginatedNews() {
      let articles = this.currentNews.articles.slice(
        this.currentNews.start,
        this.currentNews.stop
      );
      return articles;
    }
  },
  methods: {
    getNews(url) {
      axios
        .get(url)
        .then(res => (this.currentNews.articles = res.data.articles))
        .catch(err => console.log(err));
    },
    searchArticles() {
      const url = `https://newsapi.org/v2/everything?' +
          'q=${
            this.search
          }&from=2019-02-22&sortBy=publishedAt&apiKey=3077a2e3e4f94552b071d1e2666fb582`;
      axios
        .get(url)
        .then(res => console.log(res.data.articles))
        .catch(err => console.log(err.message));
    },
    saveArticle(article) {
      this.saveAlert = true
      setTimeout(() => {
         this.saveAlert = false 
      }, 2500);
      article.user = this.$session.get("user");
      db.collection("saved_articles").add(article);
    },
    nextPage() {
      console.log(this.currentNews.articles.length);
      if (this.currentNews.stop + 5 >= !this.currentNews.articles.length) {
        this.currentNews.start = this.currentNews.start += 5;
        this.currentNews.stop = this.currentNews.stop += 5;
      }
    },
    prevPage() {
      if (this.currentNews.start !== 0) {
        this.currentNews.start = this.currentNews.start -= 5;
        this.currentNews.stop = this.currentNews.stop -= 5;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.nav-item {
  font-size: 2rem;
}
img {
  height: 200px;
}
.search {
  height: 45px;
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  text-align: center;
  & input {
    font-size: 1.5rem;
  }
  & button {
    height: 90%;
  }
}
.article-container {
  display: grid !important;
  grid-template-columns: repeat(3, 25%);
  grid-template-rows: repeat;
  grid-gap: 2%;
  justify-content: center;
}
.page-nav {
  margin-top: 3%;
}
.alert {
  padding: 1%;
}
</style>



